.body{
    background-color: #FADE98;
    display: grid;
    height: 100%;
    grid-template-rows: 26% 20% 27% 16% 9%;
}
header{
    font-family: "Peddana",sans-serif;
    font-size: 96px;
    color: #4B1A0B;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.first,.second{
    width: 60vw;
    margin: auto;
    background-color: rgba(245, 198, 105, 1);
    display: grid;
    grid-template-rows: auto auto;
    border: none;
}
.second{
    width: 50vw;
}
input[type="text"],input[type="url"]{
    height: 40px;
    font-size: 20px;
    background-color: rgba(249, 238, 182, 1);
    margin: 20px 20px 0 20px;
    font-family: "Inter";
    padding: 7px;
    border: none;
    outline: none;
    box-shadow: 2px 2px 2px 0.1px rgba(0, 0, 0, 0.25);
}
input[type="text"]{
    margin-bottom: 20px;
}
#slidercontainer{
    margin: 10px auto 10px auto;
    width: calc(100% - 30px);
    border: 1px solid black;
    border-radius: 8px;
    border: 1px solid rgba(71, 70, 70, 0.778);
    position: relative;
}
input[type="range"]{
    appearance: none;
    background: #A4761E;
    width: calc(100% - 20px);
    margin: 7px 10px 7px 10px;
    border-radius: 10px;
    height: 15px;
}
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: url('/public/arrow.png') no-repeat center center; 
    background-size: 12px 12px;
    background-color: #FADE98;
    cursor: pointer;
}
#shorten{
    position: absolute;
    left: calc(50% - 75px);
    width: 150px;
}
#message{
    position: absolute;
    top:0;
    text-align: center;
    width: 240px;
    left: calc(50% - 120px); 
    background-color: antiquewhite;
    color: green;
    font-size: 18px;
    margin: 0;
    padding: 15px;
}
img{
    height: 100%;
    width: auto;
}
figure{
    display: grid;
    grid-template-rows: 54% 39%;
    gap: 7% 0;
    justify-items: center;
    align-items: center;
}
#buttonsdiv{
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    justify-content: center;
    gap: 0 50px;
}
button{
    background-color: rgba(245, 198, 105, 1);
    height: 30px;
    width: 80px;
}
